<template>
	<div class="music-list">
		<svg style="font-size: 1.6em;" class="icon" aria-hidden="true" @click="$emit('openSidebar')">
			<use xlink:href="#icon-fanhui"></use>
		</svg>
		<div class="music-list-box">
			<h3>
				个人收藏
				<svg v-show="!show" @click="show = true" style="font-size: 1.2em; float: right;" class="icon" aria-hidden="true">
					<use xlink:href="#icon-sanjiaoxing"></use>
				</svg>
				<svg v-show="show"  @click="show = false" style="font-size: 1.2em; float: right;" class="icon" aria-hidden="true">
					<use xlink:href="#icon-sanjiaoxing1"></use>
				</svg> 
			</h3>
			<transition name="musicList">
				<ul v-show="show" class="music-list-ul" >
					<p v-show="!prompt" style="text-align: center;line-height: 2em; font-size: 1.2em;">
						<span style="font-size: 1.1em;">
							空空如也,快去搜索添加入吧！
						</span>
					</p>
					<li v-for="(item,index) in musicCollectionArr.CollectionArr" :key="index">
						<img :src="item.info.songs[0].al.picUrl" alt="" class="music-list-img">
						<div class="music-list-info">
							<span class="music-list-info-musicName" :title="item.info.songs[0].name">{{item.info.songs[0].name}}</span> 
							<p>
								<span v-for="(value,index) in item.info.songs[0].ar" :key="index" :title="value.name">
									{{value.name}}
								</span>
							</p>
							
						</div>
						<div class="music-list-icon">
							<svg class="icon" aria-hidden="true" @click="removeMusic(index)">
								<use xlink:href="#icon-laji"></use>
							</svg>
							<svg class="icon" aria-hidden="true" @click="addMusic(item,0)">
								<use xlink:href="#icon-bofang2"></use>
							</svg>
						</div>
					</li>
				</ul>
			</transition>
		</div>
		<div class="music-list-box" v-if="isNewMusic">
			<h3>
				{{newMusic.name}}
				<svg v-show="!newMusic.show" @click="newMusic.show = true" style="font-size: 1.2em; float: right;" class="icon" aria-hidden="true">
					<use xlink:href="#icon-sanjiaoxing"></use>
				</svg>
				<svg v-show="newMusic.show"  @click="newMusic.show = false" style="font-size: 1.2em; float: right;" class="icon" aria-hidden="true">
					<use xlink:href="#icon-sanjiaoxing1"></use>
				</svg> 
			</h3>
			<transition name="musicList">
				<ul v-show="newMusic.show" class="music-list-ul" >
					<li v-for="(item,index) in newMusic.newMusicArr" :key="index">
						<img :src="item.picUrl" alt="" class="music-list-img">
						<div class="music-list-info">
							<span class="music-list-info-musicName">{{item.name}}</span> 
							<p>
								<span v-for="(item,index) in item.song.album.artists">
									{{item.name}}
								</span>
							</p>
							
						</div>
						<div class="music-list-icon">
							<svg class="icon" aria-hidden="true" @click="addMusic(item,1)">
								<use xlink:href="#icon-bofang2"></use>
							</svg>
						</div>
					</li>
				</ul>
			</transition>
		</div>
	</div>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				prompt: false,
				show: false,
				isNewMusic: false,
				newMusic: {},
			}
		},
		computed: {
			...mapState(['musicCollectionArr'])
		},
		watch: {

		},
		created() {
			this.get();
		},
		methods: {
			get() {
				if(this.musicCollectionArr.CollectionArr) {
					this.prompt = true;
				}else {
					this.prompt = false;
				}
				
				if(JSON.parse(localStorage.getItem('MusicNewRecommended'))) {
					this.isNewMusic = true;
					this.newMusic = JSON.parse(localStorage.getItem('MusicNewRecommended'));
				}else {
					this.isNewMusic = false;
				}
			},
			// 点击播放
			addMusic(value,bool) {
				this.$store.commit('changeMusicList', bool)
				this.$store.commit('changeId',value.id);
			},
			// 去掉收藏
			removeMusic(index) {
				this.musicCollectionArr.CollectionArr.splice(index,1);
				localStorage.setItem('musicCollectionInfo', JSON.stringify(this.musicCollectionArr))
			},
		},
	}
</script>

<style lang="scss">
	::-webkit-scrollbar {
		display: none;
	}
	.music-list {
		overflow: hidden;
		position: absolute;
		width: 70%;
		height: 80%;
		padding: 6% 3%;
		z-index: 1000;
		border-radius: 30px;
		transition: all .3s;
		background-color: rgba(216, 233, 255, 0.5);
	}
	
	.music-list-box {
		width: 100%;
		// height: 70%;
		margin: .5em 0;
		padding-top: .5em;
		border-radius: 20px;
		background-color: rgba(238, 220, 255, 0.2);
		
		h3 {
			margin: 0 0 2% 2%;
			padding: 3% 5% 6%;
			font-size: 1.2em;
		}
	}
	
	.music-list-ul {
		overflow: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 25em;
		
		li {
			width: 100%;
			height: 5em;
			margin-bottom: 4%;
			padding: 1% 4%;
			border-radius: 20px;
			background-color: rgba(216, 225, 255, 0.2);


			.music-list-img {
				display: inline-block;
				width: 3em;
				height: 3em;
				border: 4px solid #fff;
				border-radius: 50%;
				vertical-align: middle;
			}

			.music-list-info {
				overflow: hidden;
				display: inline-block;
				width: 30%;
				height: 100%;
				margin-left: 1em;
				padding: .6em 0;
				vertical-align: middle;

				// 歌名歌手名
				.music-list-info-musicName, 
				p {
					display: block;
					height: 50%;
					
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					-webkit-line-clamp: 1;
				}

				.music-list-info-musicName {
					font-size: 1.4em;
					margin-right: .4em;
				}

				p span{
					font-size: 1em;
				}
			}

			// 播放删除的图标
			.music-list-icon {
				display: inline-block;
				vertical-align: top;
				width: 40%;
				height: 100%;
				float: right;
				line-height: 5em;

				svg {
					font-size: 2em;
					margin: 0 .2em;
					cursor: pointer;
				}
			}
		}

		li:hover {
			background-color: rgba(197, 184, 229, 0.4);
		}
	}
	
	// 歌单列表动画
	.musicList-enter,
	.musicList-leave-to {
		height: 0;
	}
	
	.musicList-enter-active,
	.musicList-leave-active {
		transition: all .4s;
	}
	
	.musicList-enter-to, 
	.musicList-leave {
		height: 25em;
	}
</style>
