<template>
	<div class="play-mv">
		<svg style="font-size: 1.6em;" class="icon" aria-hidden="true" @click="$emit('opensidebar','isInterfaceTypeMenu');$emit('componentShowOrHide','isPlayList',false)">
			<use xlink:href="#icon-fanhui"></use>
		</svg>
		<ul class="play-mv-banner">
			<p>推荐</p>
			<li v-for="(item,index) in recommendedMV" :key="index" @click="recommended(item)">
				<img :src="item.picUrl" :alt="item.copywriter">
				<div class="play-mv-banner-mask">
					<span>{{item.copywriter}}</span>
				</div>
			</li>
		</ul>
		<div class="play-mv-title">
			<span :class="{active: isMv}" @click="isMv = true">分类</span>
			<span :class="{active: !isMv}" @click="isMv = false">排行</span>
		</div>
		<transition-group tag="div" name="playMv" class="play-mv-content">
			<MvType key="0" v-if="isMv"></MvType>
			<MvList key="1" v-if="!isMv"></MvList>
		</transition-group>
	</div>
</template>

<script>
	import MvList from './MvList.vue';
	import MvType from './MvType.vue';
	
	export default {
		data() {
			return {
				isMv: true,
				id: 0,
				recommendedMV: [],
				isBannerMask: false,
			}
		},
		created() {
			this.getRecommendedMV()
		},
		watch:{
		},
		methods:{
			
			// 获取最新MV
			// getNewMV() {
			// 	this.axios({
			// 		url:"/mv/first",
			// 		params: {
			// 			area: '内地' ,//全部,内地,港台,欧美,日本,韩国
			// 			limit: 20,
			// 		}
			// 	})
			// 	.then(res => {
			// 		console.log(res)
			// 	})
			// },
			
			// 获取推荐MV
			getRecommendedMV() {
				// this.axios.get("http://musicapi.leanapp.cn/personalized/mv")
				this.axios.get("https://autumnfish.cn/personalized/mv")
				.then(res => {
					// console.log(res.data.result)
					this.recommendedMV = res.data.result;
				})
			},
			
			// 获取MV信息
			// getMv(id) {
			// 	this.axios.get("https://autumnfish.cn/mv/detail?mvid=" + id)
			// 	.then(res => {
			// 		console.log(res)
			// 	})
				
			// },
			
			// 获取相似MV
			// getSimiMV() {
			// 	this.axios.get("https://autumnfish.cn/simi/mv?mvid=" + id)
			// 	.then(res => {
			// 		console.log(res)
			// 	})
			// },
			
			// 点击MV
			recommended(item) {
				this.getMvURL(item.id)
			},
			
			// 获取MV播放地址
			getMvURL(id) {
				this.axios.get("https://autumnfish.cn/mv/url?id=" + id)
				.then(res => {
					// console.log(res.data.data.url)
					window.open(res.data.data.url);  
				})
				
			},
		},
		components: {
			MvList,
			MvType,
		}
	}
</script>

<style lang="scss">
	.play-mv {
		overflow: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		padding: 1%;
		color: #ebe9ec;
		border-radius: 30px;
		background-color: rgb(48, 35, 46);
		
		.play-mv-banner {
			position: relative;
			width: 100%;
			padding: 0 2%;
			
			li {
				position: relative;
				display: inline-block;
				width: 23%;
				margin: 0 1%;
				
				img {
					width: 100%;
					border-radius: 10%;
				}
				
				.play-mv-banner-mask {
					display: none;
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					text-align: center;
					font-size: 1.4em;
					background-color: rgba(0,0,0,.5);
				}
				.play-mv-banner-mask span {
					display: block;
					position: absolute;
					bottom: 20%;
				}
				
				&:hover .play-mv-banner-mask {
					display: block;
				}
			}
		}
		
		.play-mv-title {
			padding: .5% 4%;
			
			span {
				display: inline-block;
				margin: 0 1%;
				color: #ababb9;
			}
			
			span.active {
				color: #ebebff; 
			}
		}
		
		.play-mv-content {
			padding: 1% 4%;
			
			
		}
	}
	
	.playMv-enter,
	.playMv-leave-to {
		opacity: 0;
	}
	
	.playMv-enter-active,
	.playMv-leave-active {
		transition: all .5s;
	}
	
	.playMv-enter-to,
	.playMv-leave {
		opacity: 1;
	}
</style>
