<template>
	<div class="play-list">
		<svg  v-show="isPlayList" style="font-size: 1.6em;" class="icon" aria-hidden="true" @click="$emit('opensidebar','isInterfaceTypeMenu');$emit('componentShowOrHide','isPlayList',false)">
			<use xlink:href="#icon-fanhui"></use>
		</svg>
		<svg  v-show="!isPlayList" style="font-size: 1.6em;" class="icon" aria-hidden="true" @click="isPlayList = !isPlayList">
			<use xlink:href="#icon-fanhui"></use>
		</svg>
		<transition name="playList">
			<div v-show="isPlayList" class="play-list-main">
				<div class="play-list-main-title">
					<p>{{nowPlayListName.name}}</p>
					<div class="play-list-main-alltitle"  style="margin-left: 4%; float: right;" @click="allPlayList" @mouseenter="isAllPlayList = true" @mouseleave="isAllPlayList = false">
						<span>全部</span>
						<transition name="allList">
							<div v-show="isAllPlayList">
								<span v-for="(item,index) in allPlayListNames" :key="index" @click="nowPlayListName = item">
									{{item.name}}
								</span>
							</div>
						</transition>
					</div>
					<p>
						<span style="margin: 0 .4em;" v-for="(item,index) in hotPlayListNames" :key="index" @click="nowPlayListName = item">
							{{item.name}}
						</span>
					</p>
				</div>
				<div class="play-list-main-content">
					<p class="play-list-main-type" style="margin-bottom: 2%;">
						<span @click="isPlayListType = true;isPlayListTypeId = 0" style="margin-right: 2%;" :class="{active: isPlayListTypeId == 0}">歌单</span>
						<span @click="isPlayListType = false;isPlayListTypeId = 1" :class="{active: isPlayListTypeId == 1}">精品歌单</span>
					</p>
					<transition name="playListType">
						<ul class="play-list-banner" v-if="!isPlayListType">
							<li v-for="(item,index) in bannerPlayList" :key="index">
								<img :src="item.coverImgUrl" :alt="item.copywriter">
								<p @click="playListInfo(item)">{{item.name}}</p>
								<div class="mask">
									<span>
										{{item.description}}
									</span>
								</div>
							</li>
						</ul>
					</transition>
					<transition name="playListType">
						<ul class="play-list-new" v-if="isPlayListType">
							<li v-for="(item,index) in playList" :key="index">
								<img :src="item.coverImgUrl" :alt="item.copywriter">
								<p @click="playListInfo(item)">{{item.name}}</p>
								<div class="mask">
									<span>
										{{item.description}}
									</span>
								</div>
							</li>
						</ul>
					</transition>
				</div>
			</div>
		</transition>
		<transition name="playList">
			<PlayListDetailed v-if="!isPlayList" :playListId="playListId" @goback="goSinger"></PlayListDetailed>
		</transition>
	</div>
</template>

<script>
	import PlayListDetailed from './PlayListDetailed.vue';
	export default {
		data() {
			return {
				nowPlayListName: '',
				hotPlayListNames: [],
				allPlayListNames: [],
				bannerPlayList: [],
				playList: [],
				isPlayList: true,
				isAllPlayList: false,
				playListId: '',
				isPlayListType: true,
				isPlayListTypeId: 0,
			}
		},
		created() {
			this.getPlayListName();
			this.getHotPlayListName();
			this.getGoodPlayList();
			this.getFriendsPlayList();
			// this.getRecommendedPlayList();
		},
		watch:{
			nowPlayListName: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.getGoodPlayList();
					this.getFriendsPlayList();
				}
			}
		},
		methods:{
			// 获取歌单分类名
			getPlayListName() {
				this.axios({
					// url: 'http://musicapi.leanapp.cn/playlist/catlist',
					url: 'https://autumnfish.cn/playlist/catlist',
				}).then(res => {
					// console.log('歌单分类名=>',res.data.sub);
					this.allPlayListNames = res.data.sub;
				})
			},
			
			// 获取热门歌单分类的名字
			getHotPlayListName() {
				this.axios({
					url: 'https://autumnfish.cn/playlist/hot',
				}).then(res => {
					// console.log('热门歌单分类的名字=>',res)
					this.hotPlayListNames = res.data.tags;
					this.nowPlayListName = res.data.tags[1];
				})
			},
			
			// 获取精品歌单
			getGoodPlayList() {
				this.axios({
					url: 'https://autumnfish.cn/top/playlist/highquality',
					params: {
						limit: 30,
						cat: this.nowPlayListName.name,
					}
				}).then(res => {
					// console.log('精品歌单=>',res.data.playlists)
					this.bannerPlayList = res.data.playlists;
				})
				
			},
			
			// 获取网友精品歌单（可传值
			getFriendsPlayList() {
				this.axios({
					url: 'https://autumnfish.cn/top/playlist',
					params: {
						limit: 48,
						order: 'hot', //hot,new
						cat: this.nowPlayListName.name,
						offset: 0,
					}
				}).then(res => {
					// console.log('网友精品歌单=>',res.data.playlists)
					this.playList = res.data.playlists;
				})
			},
			
			// 获取推荐歌单
			getRecommendedPlayList() {
				this.axios({
					url: 'https://autumnfish.cn/personalized',
				}).then(res => {
					console.log('推荐歌单=>',res)
				})
			},
			
			// 相关歌单推荐
			getRelatedPlayList(id) {
				// id是其他歌单的ID
				this.axios({
					url: 'https://autumnfish.cn/related/playlist?id=' + id,
				}).then(res => {
					console.log(res)
				})
			},
			
			// 获取选中歌单
			playListInfo(value) {
				this.playListId = value.id;
				this.$nextTick(() => {
					this.isPlayList = false;
				},1000)
			},
			
			// 去歌手栏
			goSinger() {
				// 先退出歌单侧边栏
				this.$emit('opensidebar','isInterfaceTypeMenu')
				// 再进入歌手页
				this.$emit('componentShowOrHide','isSearch',true)
			},
			
			// 打开全部歌单
			allPlayList() {
				this.isAllPlayList = !this.isAllPlayList;
			}
		},
		components: {
			PlayListDetailed,
		}
	}
</script>

<style lang="scss">	
	.play-list {
		width: 100%;
		height: 100%;
		padding: 1%;
		color: #ebe9ec;
		border-radius: 30px;
		background-color: rgb(48, 35, 46);
		
		.play-list-main {
			width: 100%;
			height: 90%;
			padding: 2% 4%;
			
			.play-list-main-title {
				width: 100%;
				padding: 0 0 4%;
				
				p:nth-child(1) {
					display: inline-block;
					font-size: 1.6em;
					float: left;
				}
				
				p:nth-child(3) {
					display: inline-block;
					float: right;
				}
				
				.play-list-main-alltitle {
					position: relative;
				}
				
				.play-list-main-alltitle div {
					overflow: hidden;
					position: absolute;
					top: 150%;
					right: -100%;
					width: 2000%;
					padding: 40%;
					color: #afa9c5;
					z-index: 1000;
					border-radius: 20px;
					background-color: #47364c;
				}
				
				.play-list-main-alltitle div span {
					display: inline-block;
					margin: 0 2% 2%;
					font-size: 1.4em;
					font-weight: 100;
				}
				.play-list-main-alltitle div span:hover {
					color: #cec8e3;
					font-weight: bold;
				}
			}
		}
	}
	
	.play-list-main-content {
		overflow: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		
		.play-list-main-type {
			span {
				color: #a3a8bc;
			}
			
			span:hover {
				color: #d7ddff;
				font-weight: bold;
			}
			
			span.active {
				color: #d7ddff;
				font-weight: bold;
			}
		}
		
		.play-list-new {
			width: 100%;
			text-align: center;
			
			li {
				position: relative;
				display: inline-block;
				width: 20%;
				margin: 0 2% 2%;
				
				img {
					display: block;
					width: 100%;
					height: 85%;
					border-radius: 20%;
				}
				
				p {
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					display: block;
					margin: 6% 0;
					padding: 0 4%;
					color: #d4d4d5;
					font-size: 1.4em;
				}
				
				p:hover {
					color: #aaa4c2;
				}
				
				&:hover {
					.mask {
						display: block;
					}
				}
			}
		}
		
		.play-list-banner {
			width: 100%;
			text-align: center;
			
			li {
				position: relative;
				display: inline-block;
				width: 20%;
				margin: 0 2% 2%;
				
				img {
					display: block;
					width: 100%;
					height: 85%;
					border-radius: 20%;
				}
				
				p {
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					display: block;
					margin: 6% 0;
					padding: 0 4%;
					color: #d4d4d5;
					font-size: 1.4em;
				}
				
				p:hover {
					color: #aaa4c2;
				}
				
				&:hover {
					.mask {
						display: block;
					}
				}
			}
		}
		
		.mask {
			display: none;
			position: absolute;
			top: 0;
			width: 100%;
			height: 85%;
			padding: 4%;
			color: #bcbcbd;
			text-indent: 2em;
			font-size: 1.2em;
			line-height: 1.6em;
			border-radius: 20%;
			background-color: rgba(0,0,0,.6);
			
			span {
				text-overflow: -o-ellipsis-lastline;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 5;
			}
		}
	}
	
	.allList-enter,
	.allList-leave-to,
	.playList-enter,
	.playList-leave-to,
	.playListType-enter,
	.playListType-leave-to {
		opacity: 0;
	}
	
	.allList-enter-active,
	.allList-leave-active,
	.playList-enter-active,
	.playList-leave-active, 
	.playListType-enter-active,
	.playListType-leave-active {
		transition: all .6s;
	}
	
	.allList-enter-to,
	.allList-leave,
	.playList-enter-to,
	.playList-leave,
	.playListType-enter-to,
	.playListType-leave {
		opacity: 1;
	}
	
	
	// playList
</style>
