<template>
	<div class="Lyric" >
		<ul class="Lyric-list" ref="lyric" :style="{backgroundColor: isMusicImg ? 'rgba(0, 0, 0, 0.5)' : 'rgba(255, 255, 255, 0.5)'}">
			<li v-for="(item,index) in audioMusicLyrics" :key="index" :class="{active: item.bool}">
				{{item.text}}
			</li>
		</ul>
		<div class="filter" :style="{backgroundImage: 'url('+audioMusicInfo.songs[0].al.picUrl+')'}"></div>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	export default {
		data() {
			return {
				isMusicImg: true,
				time: '',
			}
		},
		computed: {
			...mapState(['audioMusicInfo','audioMusiclyricsTime','audioMusicLyrics'])
		},
		watch: {
			audioMusiclyricsTime: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.audioMusicLyrics.forEach((item, index) => {
						if (newValue >= item.time && newValue < this.audioMusicLyrics[index + 1].time) {
							this.time = item.time;
						}
					})
				}
			},
			
			time: function(newValue,oldValue) {
				if( newValue != oldValue ) {
					this.$store.commit('changeInfolyricsColor',newValue)
				}
			}
		},
		created() {
			// this.get()
		},
		mounted() {
			// for(let key in this.$refs.lyric.children) {
			// 	if(this.$refs.lyric.children[key].className == 'active') {
			// 		console.log(this.$refs.lyric.children[key].offsetTop)
			// 	}
			// }
		},
		methods: {
			get() {
				// console.log(this.$refs.lyric);
			},
			
		}
	}
</script>

<style lang="scss">
	.Lyric {
		overflow: hidden;
		position: relative;
		width: 100%;
		height: 100%;
		border-radius: 10%;
		
		.filter {
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 20;
			filter: blur(8px);
			background-repeat: no-repeat;
			background-size: cover;
		}
	}
	
	.Lyric-list {
		overflow: hidden;
		overflow-y: scroll;
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		padding: 20% 0;
		z-index: 100;
		text-align: center;

		li {
			display: block;
			padding: 26px 0;
			font-size: 2em;
			color: #a0cae3;
			-moz-user-select:none;
			-webkit-user-select:none;
			user-select:none;    
		}

		li.active {
			color: #b48ece;
		}
	}
</style>
