<template>
	<div class="music-search-singer">
		<div class="search-music-title">
			<p>
				检索关键词：<input type="text" v-model="title" @input="searchNew"/>
			</p>
		</div>
		<ul class="search-music-list">
			<li v-for="(item,index) in songsTen" :key="index">
				<span style="font-size: 1.8em;">{{item.name}}</span>
				<svg class="icon" aria-hidden="true" @click="play(item)">
					<use xlink:href="#icon-bofang"></use>
				</svg>
			</li>
			<p style="margin-top: 20px;" v-show="isErr">暂搜不到该歌手...</p>
		</ul>
		<p class="search-music-page">
			<svg class="icon search-music-page-icon" aria-hidden="true" @click="calculate('pgUp')">
				<use xlink:href="#icon-tubiaozhizuomoban-02"></use>
			</svg>
			<span class="search-music-page-span" :class="{active: item == pageIndex}" v-for="item in pageNumber" :key="item" @click="pageIndex = item">
				{{item}}
			</span>
			<svg class="icon search-music-page-icon" aria-hidden="true" @click="calculate('pgDn')">
				<use xlink:href="#icon-tubiaozhizuomoban-01"></use>
			</svg>
		</p>
	</div>
</template>

<script>
	import { mapState } from 'vuex';
	
	export default {
		data() {
			return {
				title: '',
				songs: [],
				songsTen: [],
				pageNumber: 1,
				pageIndex: 1,
				timer: null,
				isErr: false,
			}
		},
		computed: {
			...mapState(['musicSearchSinger'])
		},
		watch:{
			pageIndex: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.songsTen = []
					for (let i in this.songs) {
						if (i <= newValue*10 && i > (newValue-1) * 10) {
							this.songsTen.push(this.songs[i])
						}
					}
				}
			},
			musicSearchSinger: function(newValue,oldValue) {
				if(newValue != oldValue) {
					console.log(newValue)
					this.searchSinger(newValue.id)
				}
			},
		},
		created() {
			this.get()
		},
		methods: {
			get() {
				this.title = this.musicSearchSinger ? this.musicSearchSinger.name : '';
				let id = this.musicSearchSinger.id
				this.searchSinger(id)
			},
			// 获取输入内容(防抖)
			searchNew() {
				if(this.timer) {
					clearTimeout(this.timer);
				}
				
				this.timer = setTimeout(() => {
					this.$store.commit('changeSearchSinger',this.title)
				},1500)
			},
			
			// 获取搜索歌手歌曲
			searchSinger(id) {
				// this.songsTen = [];
				this.axios({
					url: 'https://autumnfish.cn/artist/top/song?id=' + id,
				}).then(res => {
					if(res.data.songs) {
						this.isErr = false;
						this.songs = res.data.songs;
						if(Math.ceil(this.songs.length / 10) < 15) {
							this.pageNumber = Math.ceil(this.songs.length / 10)
						}
						for (let i in this.songs) {
							// console.log(i)
							if (i < 10) {
								this.songsTen.push(this.songs[i])
							}
						}
					}else {
						this.isErr = true;
					}
				})
			},
			
			// 播放
			play(item) {
				this.$store.commit('changeId',item.id);
			},
			
			calculate(index) {
				if(index == 'pgUp') {
					if(this.pageIndex > 1) {
						this.pageIndex --;
					}
				}else {
					if(this.pageIndex < this.pageNumber) {
						this.pageIndex ++;
					}
				}
			},
		},
	}
</script>

