<template>
	<div class="music-library">
		<svg style="font-size: 1.6em; margin-bottom: 2%;" class="icon" aria-hidden="true" @click="$emit('openSidebar')">
			<use xlink:href="#icon-fanhui"></use>
		</svg>
		
		<div class="music-library-type">
			<ul v-for="(item,index) in libraryName" :key="index" :class="{active: index == id}" @click="goWitch(index)">
				{{item.name}}
			</ul>
		</div>
	</div>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				libraryName: [
					{
						name: '歌单',
						type: ['分类','网友精选','精品推荐']
						
					},
					{
						name: 'MV',
						type: ['MV分类','最新MV','MV排行']
					},
				],
				id: 0,
			}
		},
		computed: {
			...mapState(['musicCollectionArr'])
		},
		watch: {

		},
		created() {
			this.goWitch(0);
		},
		methods: {
			get() {
				
			},
			goWitch(index) {
				this.id = index;
				if(index == 0) {
					this.$emit('componentShowOrHide','isPlayList',true);
				}else {
					this.$emit('componentShowOrHide','isMv',true);
				}
			},
		},
	}
</script>

<style lang="scss">
	::-webkit-scrollbar {
		display: none;
	}
	.music-library {
		overflow: hidden;
		position: absolute;
		width: 40%;
		// height: 20%;
		padding: 6% 3%;
		z-index: 1000;
		border-radius: 30px;
		transition: all .3s;
		background-color: rgba(216, 233, 255, 0.5);
		
		.music-library-type {
			
			ul {
				display: block;
				margin: 20% 0;
				padding: 10%;
				font-size: 1.4em;
				
				&:hover {
					color: #4a4663;
					font-weight: bold;
				}
				
				&.active {
					margin: 6.5% 0;
					font-size: 2em;
					font-weight: bold;
				}
			}
		}
		
		&:hover {
			width: 50%;
		}
	}
	
	// 歌单列表动画
	.musicList-enter,
	.musicList-leave-to {
		height: 0;
	}
	
	.musicList-enter-active,
	.musicList-leave-active {
		transition: all .4s;
	}
	
	.musicList-enter-to, 
	.musicList-leave {
		height: 10em;
	}
</style>
