<template>
	<div class="music-player">
		<div class="music-player-img">
			<img :src="musicImg" alt="" :class="{active: imgRotating}" />
		</div>
		<div class="music-player-progress">
			<div class="music-player-info">
				<audio style="height: 0;" ref="audio_music" :src="audioMusicUrl" controls="controls" @timeupdate="currentTime" @abort="onabort" @loadeddata="onloadeddata"
				 @ended="onended" @durationchange="ondurationchange"></audio>
				<div style="margin-bottom: 16px;">
					<p>
						<span style="font-size: 1.2em; margin-right: 10px;">{{musicName}}</span>
						<span style="font-size: .4em;" v-for="(item,index) in singerName" :key="index">
							{{item.name}}
						</span>
					</p>
					<p style="float: right;">
						<span>{{musicTimeNew}}</span> /
						<span>{{musicTimeMax}}</span>
					</p>
				</div>
				<div class="component-progress" @click="setMusicProgressClick">
					<div class="component-progress-width" ref="progress_width"></div>
					<span class="component-progress-ball" ref="ball"></span>
				</div>
			</div>
			<div class="music-player-func">
				<!-- 收藏 -->
				<svg class="icon music-player-small music-player-collection" aria-hidden="true" :class="{active: collectionColor}"
				 @click="collection">
					<use xlink:href="#icon-collection"></use>
				</svg>
				<!-- 左切歌 -->
				<svg class="icon music-player-big" aria-hidden="true" @click="playPrev">
					<use xlink:href="#icon-tubiaozhizuomoban-02"></use>
				</svg>
				<!-- 播放 -->
				<svg class="icon music-player-big" aria-hidden="true" v-show="playOrSuspended" @click="musicPlaying">
					<use xlink:href="#icon-bofang"></use>
				</svg>
				<!-- 暂停 -->
				<svg class="icon music-player-big" aria-hidden="true" v-show="!playOrSuspended" @click="musicPause">
					<use xlink:href="#icon-bofang1"></use>
				</svg>
				<!-- 右切歌 -->
				<svg class="icon music-player-big" aria-hidden="true" @click="playNext">
					<use xlink:href="#icon-tubiaozhizuomoban-01"></use>
				</svg>
				<!-- 音量 -->
				<div class="voice" @mouseenter="setVoice">
					<svg class="icon music-player-small" aria-hidden="true">
						<use xlink:href="#icon-yinliang"></use>
					</svg>
					<input type="range" v-model="rangeSize" v-show="isRange">
				</div>
				<!-- 搜索 -->
				<div class="searchMusic" @mouseenter="setSearch" @keydown.enter="onSearch">
					<svg class="icon music-player-small" aria-hidden="true" style="font-size: 2.4em" @click="onSearch">
						<use xlink:href="#icon-sousuo"></use>
					</svg>
					<input ref="inputText" type="text" class="searchMusic-icon" v-show="isSerach" v-model="searchText" @blur="blur" @input="input" @mouseleave="isSerach = false">
					<ul class="searchMusic-detail" v-show="searchText">
						<li v-for="(item,index) in searchName" :key="index" @click="inputName(item)">
							{{item}}
						</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="music-player-search">
			<canvas width="200" height="100" ref="canvas"></canvas>
		</div>
	</div>
</template>

<script>
	import {
		mapState,
		mapGetters,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				musicImg: '',
				isMusicImg: false,
				musicID: '',
				musicTimeMax: '0.00',
				musicTimeNew: '0.00',
				rangeSize: 50,
				isRange: false,
				isSerach: false,
				searchText: '',
				playOrSuspended: true,
				collectionColor: false,
				searchSongInfo: [],
				singerName: '',
				musicName: '',
				lyricsTime: [],
				imgRotating: false,
				trackTimer: null,
				musicCollectionInfo: [],
				timer: null,
				searchName: [],
			}
		},
		created() {
			this.get();
		},
		watch: {
			rangeSize: function(newValue, oldValue) {
				if (newValue != oldValue) {
					this.$refs.audio_music.volume = newValue / 100;
				}

			},
			audioMusicId: function(newValue, oldValue) {
				if (newValue != oldValue) {
					this.getMusicUrl();
					this.collectionColor = false;
					for (let i in this.musicCollectionArr.CollectionArr) {
						if (this.musicCollectionArr.CollectionArr[i].id == this.audioMusicId) {
							this.collectionColor = true;
						}
					}
				}
			},
			audioMusicUrl: function(newValue, oldValue) {
				if (newValue != oldValue) {
					this.$nextTick(() => {
						if (newValue) {
							this.getSongInfo(this.audioMusicId);
							this.getLyrics(this.audioMusicId);
						}
					})
				}
			},
			audioMusicInfo: function(newValue, oldValue) {
				if (newValue != oldValue) {
					this.$nextTick(() => {
						this.musicImg = newValue.songs[0].al.picUrl;
						this.musicName = newValue.songs[0].name;
						this.singerName = newValue.songs[0].ar;
						if(this.playBool) {
							this.musicPlaying();
						}
					})
				}
			},
			musicCollectionArr: function(newValue, oldValue) {
				if (newValue != oldValue) {
					localStorage.setItem('musicCollectionInfo', JSON.stringify(newValue));
				}
			},

		},
		computed: {
			...mapState(['audioMusicUrl', 'audioMusicId', 'audioMusicInfo', 'audioMusiclyricsTime', 'musicCollectionArr',
				'musicNewCommendedArr','playBool'
			])
		},
		methods: {
			get() {
				let obj = localStorage.valueOf();
				for (let key in obj) {
					if (key.slice(0, 19) == 'musicCollectionInfo') {
						let top = JSON.parse(obj[key]);
						this.$store.commit('changeCollectionArr', top)
					}
				}

				let commended = JSON.parse(localStorage.getItem('MusicNewRecommended'));
				if (commended) {
					if (new Date().getMonth() + 1 == commended.time.Month) {
						if (commended.time.day < new Date().getDate()) {
							this.getNewMusic();
						} else {
							this.$store.commit('changeCommended', commended);
							// console.log('新歌推荐今日已更新！')
						}
					} else {
						this.getNewMusic();
					}
				} else {
					this.getNewMusic();
				}
				this.$store.commit('changeId',this.musicNewCommendedArr.newMusicArr[0].id);
			},

			// 获取音乐 url
			getMusicUrl() {
				this.axios.get('https://autumnfish.cn/song/url?id=' + this.audioMusicId).then(res => {
					this.$store.commit('changeUrl', res.data.data[0].url)
					// this.musicPlaying()
				})
			},

			// 获取推荐新音乐
			getNewMusic() {
				this.axios({
					url:'https://autumnfish.cn/personalized/newsong',
					// url:'http://musicapi.leanapp.cn/personalized/newsong',
				})
				.then(res => {
					if (res.data.result) {
						this.$store.commit('changeCommended', res.data.result);
						localStorage.setItem('MusicNewRecommended', JSON.stringify({
							time: {
								year: new Date().getFullYear(),
								Month: new Date().getMonth() + 1,
								day: new Date().getDate(),
							},
							newMusicArr: res.data.result,
							name: '新歌推荐',
							show: false,
						}))
					}

				})
			},

			// 获取歌曲详情
			getSongInfo(id) {
				this.axios({
					url: 'https://autumnfish.cn/song/detail?ids=' + id
				}).then(res => {
					this.$store.commit('changeInfo', res.data);
				})
			},

			// 获取歌词
			getLyrics(id) {
				this.axios({
					url: 'https://autumnfish.cn//lyric?id=' + id
				}).then(res => {
					let lyricsTime = [];
					if (res.data.lrc) {
						let lyricArr = res.data.lrc.lyric.split(/\n/)
						lyricArr.map(l => {
							let lrc = l.split(']')
							let times = lrc[0].substr(1).split(':')
							lyricsTime.push({
								time: parseFloat(Number(times[0] * 60) + Number(times[1])).toFixed(2),
								text: lrc[1],
								bool: false
							})
						})
					}
					this.$store.commit('changeInfolyrics', lyricsTime)
				})
			},

			// 实时获取播放状态
			currentTime(e) {
				// 保存到Vuex
				this.$store.commit('changeInfolyricsTime', Number(parseFloat(e.target.currentTime).toFixed(2)));

				this.musicTimeMax = parseFloat(e.target.duration / 60).toFixed(2) > 0 ? parseFloat(e.target.duration / 60).toFixed(
					2) : '0.00';
				this.musicTimeNew = parseInt(e.target.currentTime) < 60 ? '0.' + parseInt(e.target.currentTime) : parseFloat(
					parseInt(e.target.currentTime) / 60).toFixed(2);

				if (this.$refs.ball) {
					this.$refs.ball.style.left = parseFloat(((this.$refs.ball.parentElement.offsetWidth - this.$refs.ball.offsetWidth) /
						e.target.duration) * e.target.currentTime).toFixed(2) + 'px'
					this.$refs.progress_width.style.width = parseFloat(((this.$refs.ball.parentElement.offsetWidth - this.$refs.ball.offsetWidth) /
						e.target.duration) * e.target.currentTime).toFixed(2) + 'px'
				}
			},

			// 当媒体被中止时
			onabort() {

				// 歌曲封面中止旋转
				this.imgRotating = false;
				this.playOrSuspended = true;
			},

			// 当切换歌曲时
			ondurationchange() {
				this.$store.commit('changeInfolyricsTime', 0);
				this.$refs.ball.style.left = 0;
				this.$refs.progress_width.style.width = 0;
			},
			// 音乐播放完
			onended() {
				this.playOrSuspended = true;
				this.playNext();
				// clearInterval(this.trackTimer)
			},

			// 页面加载完播放媒介
			onloadeddata() {
				// this.setTrack();
			},

			// 播放歌单下一首歌
			playNext() {
				if(this.musicCollectionArr.length != 0) {
					if(this.$store.state.musicListNumber == 0) {
						let bool = true;
						for(let i in this.musicCollectionArr.CollectionArr) {
							if(this.audioMusicId == this.musicCollectionArr.CollectionArr[i].id) {
								bool = false;
								if(i < (this.musicCollectionArr.CollectionArr.length - 1)) {
									this.$store.commit('changeId',this.musicCollectionArr.CollectionArr[(parseInt(i)+1)].id);
									return;
								}else {
									this.$store.commit('changeId',this.musicCollectionArr.CollectionArr[0].id);
									return;
								}
							}
						}
						if (bool) {
							this.$store.commit('changeId', this.musicCollectionArr.CollectionArr[0].id);
						}
					}else {
						let bool = true;
						for(let i in this.musicNewCommendedArr.newMusicArr) {
							if(this.audioMusicId == this.musicNewCommendedArr.newMusicArr[i].id) {
								bool = false;
								if(i < (this.musicNewCommendedArr.newMusicArr.length - 1)) {
									this.$store.commit('changeId',this.musicNewCommendedArr.newMusicArr[(parseInt(i)+1)].id);
									return;
								}else {
									this.$store.commit('changeId',this.musicNewCommendedArr.newMusicArr[0].id);
									return;
								}
							}
						}
						if (bool) {
							this.$store.commit('changeId', this.musicNewCommendedArr.newMusicArr[0].id);
						}
					}
				}
			},
			// 播放歌单上一首歌
			playPrev() {
				if(this.musicCollectionArr.length != 0) {
					if(this.$store.state.musicListNumber == 0) {
						let bool = true;
						for(let i in this.musicCollectionArr.CollectionArr) {
							if(this.audioMusicId == this.musicCollectionArr.CollectionArr[i].id) {
								bool = false;
								if (i > 0) {
									this.$store.commit('changeId', this.musicCollectionArr.CollectionArr[(parseInt(i)-1)].id);
									return;
								}else {
									this.$store.commit('changeId', this.musicCollectionArr.CollectionArr[this.musicCollectionArr.CollectionArr.length - 1].id);
									return;
								}
							}
						}
						if (bool) {
							this.$store.commit('changeId', this.musicCollectionArr.CollectionArr[0].id);
						}
					}else {
						let bool = true;
						for(let i in this.musicNewCommendedArr.newMusicArr) {
							if(this.audioMusicId == this.musicNewCommendedArr.newMusicArr[i].id) {
								bool = false;
								if (i > 0) {
									this.$store.commit('changeId', this.musicNewCommendedArr.newMusicArr[(parseInt(i)-1)].id);
									return;
								}else {
									this.$store.commit('changeId', this.musicNewCommendedArr.newMusicArr[this.musicNewCommendedArr.newMusicArr.length - 1].id);
									return;
								}
							}
						}
						if (bool) {
							this.$store.commit('changeId', this.musicNewCommendedArr.newMusicArr[0].id);
						}
					}
				}
			},

			// 点击修改歌曲进度
			setMusicProgressClick(e) {
				if (this.audioMusicUrl.length != 0) {
					if (e.path[0].className == 'component-progress') {
						e.target.children[1].style.left = (e.pageX - e.target.offsetLeft) - e.target.children[1].offsetWidth / 2 + 'px';
						let musicOneTime = this.$refs.audio_music.duration / (e.path[0].offsetWidth - this.$refs.ball.offsetWidth)
						this.$refs.audio_music.currentTime = parseFloat(((e.pageX - e.target.offsetLeft) - e.target.children[1].offsetWidth /
							2) * musicOneTime).toFixed(6)
					} else if (e.path[0].className == 'component-progress-ball') {
						e.target.style.left = (e.pageX - e.target.parentElement.offsetLeft) - e.target.offsetWidth / 2 + 'px';
					}
				}
			},

			// 播放
			musicPlaying() {
				// console.log(this.audioMusicUrl)
				if (this.audioMusicUrl) {
					this.isMusicImg = true;
					this.playOrSuspended = false;
					this.$refs.audio_music.play()
					// 歌曲封面开始旋转
					this.imgRotating = true;
				}
			},

			// 暂停
			musicPause() {
				this.onPause();
				this.$refs.audio_music.pause()
			},
			onPause() {
				this.playOrSuspended = true;
				// 歌曲封面中止旋转
				this.imgRotating = false;
			},

			// 收藏
			collection() {
				let Collection = {
					'CollectionArr': [],
					'name': '个人收藏',
					'show': true,
				};
				let bool = true;
				if (this.audioMusicUrl) {
					let obj = JSON.parse(localStorage.getItem('musicCollectionInfo'))
					if (obj) {
						Collection = obj;
						for(let key in obj.CollectionArr) {
							if(obj.CollectionArr[key] == this.audioMusicId) {
								bool = false;
							}
						}
						if(bool) {
							this.collectionColor = true;
							Collection.CollectionArr.push({
								id: this.audioMusicId,
								info: this.audioMusicInfo,
								bool: true,
							})
							this.$store.commit('changeCollectionArr', Collection)
						}
					} else {
						this.collectionColor = true;
						Collection.CollectionArr.push({
							id: this.audioMusicId,
							info: this.audioMusicInfo,
							bool: true,
						})
						this.$store.commit('changeCollectionArr', Collection)
					}
				}
			},

			// 音轨跳动
			// setTrack() {
			// 	let wrap = this.$refs.canvas
			// 	let cxt = wrap.getContext("2d");
			// 	// //获取API
			// 	let AudioContext = window.AudioContext || window.webkitAudioContext;
			// 	let context = new AudioContext;
			// 	// //加载媒体
			// 	let audio = this.$refs.audio_music;
			// 	// //创建节点
			// 	let source = context.createMediaElementSource(audio);
			// 	let analyser = context.createAnalyser();
			// 	// //连接：source → analyser → destination
			// 	source.connect(analyser);
			// 	analyser.connect(context.destination);
			// 	// //创建数据
			// 	let output = new Uint8Array(10);
			// 	// // console.log(output)
			// 	let du = 2; //角度
			// 	let number = 10; //数量
			// 	let potInt = {
			// 		x: 0,
			// 		y: 100,
			// 	}; //起始坐标
			// 	// let R = 10; //半径
			// 	let W = 10; //宽

			// 	let audioColor = [];
			// 	for (let i = 0; i < number; i++) {
			// 		audioColor[i] = 'rgba(' + 255 + ',' + Math.floor(Math.random() * 55 + 200) + ',' + Math.floor(Math.random() * 50 +
			// 			205) + ',.8)'
			// 	}
			// 	clearInterval(this.trackTimer)
			// 	this.trackTimer = setInterval(() => {
			// 		analyser.getByteFrequencyData(output); //获取频域数据
			// 		cxt.clearRect(0, 0, wrap.width, wrap.height);
			// 		for (var i = 0; i < number; i++) {
			// 			var value = output[i] / 2; //<===获取数据 
			// 			cxt.beginPath();
			// 			cxt.lineWidth = W;
			// 			cxt.strokeStyle= audioColor[i];
			// 			cxt.moveTo(potInt.x + i * 10 , (potInt.y));
			// 			cxt.lineTo(potInt.x + i * 10 , (potInt.y - value));
			// 			cxt.stroke();
			// 		}
			// 	}) 	
			// },

			// 显示音量条
			setVoice(e) {
				this.isRange = true;
				e.target.onmouseleave = () => {
					this.isRange = false;
				}
			},

			// 显示搜索框
			setSearch(e) {
				this.isSerach = true;
			},
			// 搜索输入框失去焦点时
			blur() {
				if(this.searchText.length <= 0) {
					this.isSerach = false;
				}
			},
			// 点击搜索
			onSearch() {
				if (this.searchText.length != 0) {
					this.$store.commit('changeSearch', this.searchText)
					this.searchText = '';
					this.isSerach = false;
					this.$store.commit('changeIsSearch', true);
					this.$emit('componentShowOrHide', 'isSearch', 'true');
				}
			},
			input() {
				if(this.timer) {
					clearTimeout(this.timer);
				}
				
				this.timer = setTimeout(() => {
					this.getSearch(this.searchText)
				},1000)
			},
			
			// 点击进去
			inputName(item) {
				this.searchText = item;
				this.onSearch();
			},
			
			// 搜索输入时,获取联想词
			getSearch(musicName) {
				this.searchName = [];
				this.axios({
					url: 'https://autumnfish.cn/cloudsearch?keywords=' + musicName,
				}).then(res => {
					if(res.data.result.songs) {
						for(let i=0; i < 6; i++) {
							this.searchName.push(res.data.result.songs[i].name)
						}
					}else {
						this.searchName = ['暂无数据'];
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	.music-player {
		display: flex;
		width: 100%;
		height: 100%;
		// border: 5px solid rgb(76, 86, 139);

		.music-player-progress,
		.music-player-img,
		.music-player-search {
			height: 100%;
			padding: 20px;
			z-index: 10;
			background-color: rgba(10, 5, 36, 0.8);
		}

		.music-player-img,
		.music-player-search {
			flex: 2;
		}

		.music-player-search {}

		.music-player-img {
			text-align: center;

			img {
				display: inline-block;
				width: 150px;
				height: 150px;
				border-radius: 50%;
				border: 6px solid rgb(201, 222, 222);
				vertical-align: middle;
			}

			img.active {
				animation: myfirst 8s linear infinite;
			}

		}
	}

	// 自定义播放器功能样式
	.music-player-progress {
		flex: 6;

		.music-player-info {
			width: 100%;
			// width: 1000px;
			height: 50%;
			font-size: 1.4em;
			color: #cecece;

			p {
				display: inline-block;
			}

			.component-progress {
				position: relative;
				width: 100%;
				height: 16px;
				border: 3px solid black;
				border-radius: 10px;
				background-color: rgba(185, 185, 185, 0.2);

				.component-progress-width {
					width: 0;
					height: 10px;
					background-color: rgb(99, 0, 0);
				}

				.component-progress-ball {
					position: absolute;
					display: block;
					top: -13px;
					left: 0;
					width: 36px;
					height: 36px;
					border: 8px solid rgba(88, 95, 111, .8);
					border-radius: 50%;
					background-color: rgba(177, 76, 74, .8);
				}
			}
		}

		.music-player-func {
			width: 100%;
			height: 50%;
			padding-top: 2%;
			text-align: center;

			.icon {
				display: inline-block;
				margin: 0 16px;
				vertical-align: middle;
				color: rgb(75, 103, 124);

				&:hover {
					color: rgb(129, 165, 208);
				}
			}

			.music-player-collection {

				&.active {
					color: rgb(193, 106, 125);
				}

			}

			.music-player-big {
				font-size: 4em;
			}

			.music-player-small {
				font-size: 3.4em;
			}

			.voice {
				display: inline-block;

				input {
					display: inline-block;
					width: 100px;
					vertical-align: middle;
					margin-right: 10px;
				}
			}

			.searchMusic {
				position: relative;
				display: inline-block;
				margin-top: 10px;
				text-align: left;

				.icon {
					margin: 0 12px 0 4px;
				}

				.searchMusic-icon {
					display: inline-block;
					height: 20%;
					border: 0;
