<template>
	<div class="mv-type">
		<div class="my-types">
			<p v-for="(item,index,n) in type" :key="index">
				<span style="display: inline-block; width: 5%; margin: 0 1% 1% 0; text-align: right; color: #7e83ab;">{{index}} :</span>
				<span style="margin-right: 1%;" v-for="(value,i) in item" :key="i" :class="{active: value == typeId[index]}" @click="changeMvType(value,index)">
					{{value}}
				</span>
			</p>
		</div>
		<div class="mv-type-content">
			<ul>
				<li v-for="(item,index) in MVtypeList" :key="index">
					<img :src="item.cover" :alt="item.name">
					<p :title="item.name">{{item.name}}</p>
					<svg class="icon" aria-hidden="true" @click="recommended(item)">
						<use xlink:href="#icon-bofang"></use>
					</svg>
				</li>
			</ul>
			<div class="mv-type-content-progress">
				<span @click="changeOffset('page')">上页</span>
				<input v-model="textOffset" type="text" @keydown.enter="offset = (textOffset-1) * 10">
				<span @click="changeOffset('next')">下页</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				type: {
					'地区': ['全部','内地','港台','欧美','日本','韩国'],
					'发布源': ['全部','官方版','原生','现场版','网易出品'],
					'状态': ['最热','最新','上升最快'],
				},
				typeId: {
					'地区': '全部',
					'发布源': '全部',
					'状态': '上升最快',
				},
				MVtypeList: [],
				textOffset: 1,
				offset: 0,
			}
		},
		created() {
			this.getMV()
		},
		watch: {
			offset: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.getMV();
				}
			}
		},
		methods: {
			// 获取全部MV
			getMV() {
				this.axios({
					url: "https://autumnfish.cn/mv/all",
					params: {
						area: this.typeId['地区'] ,//全部,内地,港台,欧美,日本,韩国
						type: this.typeId['发布源'], //全部,官方版,原生,现场版,网易出品
						order: this.typeId['状态'], //最热,最新,上升最快
						limit: 30, //数据数量
						offset: this.offset, //数据在第几页
					}
				})
				.then(res => {
					this.MVtypeList = res.data.data;
				})
			},
			
			changeMvType(value,index) {
				for(let key in this.typeId) {
					if(key == index) {
						if(this.typeId[key] == value) {
							return
						}
						this.typeId[index] = value;
						this.getMV();
						this.offset = 0;
						this.textOffset = 1;
					}
				}
			},
			
			// 点击MV
			recommended(item) {
				this.getMvURL(item.id)
			},
			
			// 获取MV播放地址
			getMvURL(id) {
				this.axios.get("https://autumnfish.cn/mv/url?id=" + id)
				.then(res => {
					// console.log(res.data.data.url)
					window.open(res.data.data.url);  
				})
				
			},
			
			changeOffset(state) {
				if(state == 'next') {
					this.offset += 30;
				}else if(state == 'page' && this.offset >= 30){
					this.offset -= 30;
				}
				this.textOffset = (this.offset/30)+1;
			},
		},
	}
</script>

<style lang="scss">
	.mv-type {
		
		.my-types {
			p {
				span {
					color: #bfbfbf;
				}
				
				span.active {
					color: #ffffff;
				}
			}
		}
	}
	
	.mv-type-content {
		width: 100%;
		
		ul {
			width: 100%;
			
			li {
				position: relative;
				display: inline-block;
				width: 20%;
				margin: 1% 0;
				text-align: center;
				
				img {
					width: 80%;
					border-radius: 10%;
				}
				
				p {
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					display: inline-block;
					width: 70%;
					margin: 5% 0 4%;
					color: #b5bed6;
					font-size: 1em;
				}
				
				svg {
					display: none;
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					color: #d2d2ff; 
					margin: auto;
					font-size: 3em;
					cursor: pointer;
				}
			}
			
			li:hover svg {
				display: block;
			}
		}
		
		.mv-type-content-progress {
			text-align: center;
			
			span {
				
			}
			
			input {
				width: 2em;
				margin: 0 1em;
				padding: .5em;
				border: 0px;
				outline: none;
				border-radius: 10px;
				background-color: rgba(106, 138, 255, 0.4);
			}
		}
	}
</style>
