<template>
	<div id="music" @click="$store.commit('changePlayBool', true)">
		<div class="music-bgImg" :style="{backgroundImage}"></div>
		<div class="music-interface">
			<!-- 侧边栏 -->
			<div class="interface-type">
				<!-- 用户 -->
				<div class="interface-type-info" key="0">
					<div class="user-portrait">
						<img src="" alt="" v-if="isBool.isPortrait">
						<img src="../public/images/user.jpg" alt="" v-if="!isBool.isPortrait">
					</div>
					<div class="user-info">
						<span v-show="isBool.isPortrait"></span>
						<span v-show="!isBool.isPortrait" @click="login">登录 / 注册</span>
					</div>
				</div>
				<!-- 侧边栏组件 -->
				<transition name="anime">
					<InterfaceTypeMenu v-if="isSidebar.isInterfaceTypeMenu" @openLyrics="openLyrics" @opensidebar="opensidebar" @componentShowOrHide="componentShowOrHide" key="1"></InterfaceTypeMenu>
				</transition>
				<transition name="anime">
					<MusicList v-if="isSidebar.isMusicList" @openSidebar="openSidebar"></MusicList>
				</transition>
				<transition name="anime">
					<MusicLibrary v-if="isSidebar.isMusicLibrary" @openSidebar="openSidebar" @componentShowOrHide="componentShowOrHide"></MusicLibrary>
				</transition>
			</div>
			<!-- 内容区 -->
			<transition-group class="interface-content" tag="div" name="contentAnime">
				<Lycics key="1" v-if="isBool.isLycics"></Lycics>
				<Search key="2" v-if="isBool.isSearch" @searchSongs="componentShowOrHide"></Search>
				<PlayList key="3" v-if="isBool.isPlayList" @componentShowOrHide="componentShowOrHide" @opensidebar="opensidebar"></PlayList>
				<Mv key="4" v-if="isBool.isMv" @componentShowOrHide="componentShowOrHide" @opensidebar="opensidebar"></Mv>
			</transition-group>
		</div>
		<!-- 自定义播放器组件 -->
		<div class="music-play">
			<MusicPlay @componentShowOrHide='componentShowOrHide'></MusicPlay>
		</div>
	</div>
</template>
<script>
	import {mapState} from 'vuex'
	import InterfaceTypeMenu from './components/interface-type/InterfaceTypeMenu.vue'
	import Search from './components/search/Search.vue';
	import Lycics from './components/lyrics/Lyrics.vue';
	import MusicPlay from './components/MusicPlay.vue';
	import MusicList from './components/interface-type/musiclist/Musiclist.vue';
	import MusicLibrary from './components/interface-type/musiclibrary/MusicLibrary.vue';
	import PlayList from './components/musi-charts/playlist/PlayList.vue';
	import Mv from './components/musi-charts/mv/Mv.vue';

	export default {
		data() {
			return {
				isSidebar: {
					isInterfaceTypeMenu: true,
					isMusicList: false,
					isMusicLibrary: false,
				},
				isBool: {
					isPortrait: false,
					isSearch: false,
					isLycics: false,
					isPlayList: false,
					isMv: false,
				},
				backgroundImage: '',
			}
		},
		created() {
			
		},
		watch: {
			audioMusicInfo: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.backgroundImage = 'url('+newValue.songs[0].al.picUrl+')';
				}
			},
		},
		computed:{
			...mapState(['audioMusicLyrics','audioMusicInfo'])
		},
		methods: {
			login() {

			},
			
			// 关闭所有侧边栏组件
			downSidebar() {
				for(let i in this.isSidebar) {
					this.isSidebar[i] = false;
				}
			},
			// 打开歌单,同时关闭侧边栏
			opensidebar(name) {
				this.downSidebar();
				for(let i in this.isSidebar) {
					if(i == name) {
						this.isSidebar[i] = true;
					}
				}
			},
			// 回到侧边栏
			openSidebar() {
				this.downSidebar();
				this.isSidebar.isInterfaceTypeMenu = true;
			},
			
			
			// 关闭所有组件
			downComponent() {
				for(let i in this.isBool) {
					this.isBool[i] = false;
				}
			},
			// 打开歌词组件
			openLyrics() {
				if(this.audioMusicLyrics.length != 0) {
					this.downComponent();
					this.isBool.isLycics = true;
				}
			},
			
			// 显示/隐藏组件
			componentShowOrHide(componentName,bool) {
				for(let i in this.isBool) {
					this.isBool[i] = false;
					if(i == componentName) {
						if(bool) {
							this.isBool[i] = true;
						}else {
							this.isBool[i] = false;
						}
					}
				}
			},
		},
		components: {
			InterfaceTypeMenu,
			Search,
			Lycics,
			MusicPlay,
			MusicList,
			MusicLibrary,
			PlayList,
			Mv,
			
		}
	}
</script>

<style lang="scss">
	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}
	::-webkit-scrollbar {
		display: none;
	}

	#music {
		position: relative;
		min-width: 20rem;
		height: 100vh;
		-moz-user-select: none;
		-webkit-user-select: none;
		user-select: none;
		// text-align: center;
		
		// 背景图样式
		.music-bgImg {
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
			background-size: cover;
			background-repeat: no-repeat;
			background-color: rgba(13, 7, 47, 0.5);
		}
		
		// 主要内容/功能区
		.music-interface {
			height: 84%;
			padding-right: 2%;
			padding: 3% 2%;
			background-color: rgba(13, 7, 47, 0.4);

			.interface-type,
			.interface-content {
				display: inline-block;
				height: 100%
			}

			.interface-type {
				width: 20%;
				float: left;
			}

			.interface-content {
				width: 76%;
				float: right;
			}
		}

		.interface-type .interface-type-info {
			max-width: 80%;
			height: 16%;
			border-radius: 30px;

			.user-portrait,
			.user-info {
				display: inline-block;
				// width: 50%;
				// height: 100%;
				box-sizing: border-box;
				vertical-align: middle;
			}

			.user-portrait {
				padding: 3%;
				text-align: center;

				img {
					display: inline-block;
					width: 70PX;
					height: 70PX;
					vertical-align: top;
					border-radius: 50%;
				}
			}

			.user-info {
				span {
					font-size: 1.4em;
					color: #8e9ea6;
				}
			}

		}
		
		.interface-type {
			position: relative;
		}
		.interface-type .interface-type-menu {
			overflow: hidden;
			position: absolute;
			width: 26%;
			height: 80%;
			padding: 0 8%;
			border-radius: 30px;
			transition: all .3s;
			background-color: rgba(216, 233, 255, 0.6);

			li {
				width: 100%;
				color: #434859;
				transition: all .3s;

				svg {
					display: inline-block;
					font-size: 2.6em;
					margin: 40px 0;
					vertical-align: middle;
				}

				span {
					// display: inline-block;
					display: none;
					margin-left: 20px;
					font-size: 1.6em;
				}

				&:hover {
					font-size: 1.4em;
					margin: -4px 0;
					color: #000000;
				}
			}

			&:hover {
				width: 70%;
				border-top-right-radius: 40%;
				border-bottom-right-radius: 40%;

				li span {
					display: inline-block;
				}
			}
		}

		// 自定义播放器
		.music-play {
			display: flex;
			height: 16%;
		}
		
	}

	// 侧边栏功能区样式
	.interface-type {
		// background-color: rgba(13, 7, 47, 0.8);
	}

	// 侧边栏显示隐藏动画
	.anime-enter,
	.anime-leave-to {
		transform: translateX(-100%);
	}
	
	.anime-enter-active,
	.anime-leave-active {
		transition: all .5s;
	}
	
	.anime-enter-to,
	.anime-leave {
		transform: translateX(0%);
	}
	
	
	// 内容组件动画
	.contentAnime-enter,
	.contentAnime-leave-to {
		opacity: 0;
	}
	
	.contentAnime-enter-active,
	.contentAnime-leave-active {
		transition: all .5s;
	}
	
	.contentAnime-enter-to,
	.contentAnime-leave {
		opacity: 1;
	}
</style>
