<template>
	<div class="mv-list">
		<p class="mv-list-titel">
			<span>地区：</span>
			<span style="margin-right: 1%;" v-for="(item,index) in type" :key="index" :class="{active: item == typeNew}" @click="typeNew = item;offset = 0;textOffset = 1;">
				{{item}}
			</span>
		</p>
		<ul class="mv-list-content">
			<li v-for="(item,index) in MvList" :key="index">
				<img :src="item.cover" :alt="item.name">
				<p :title="item.name">{{item.name}}</p>
				<svg class="icon" aria-hidden="true" @click="recommended(item)">
					<use xlink:href="#icon-bofang"></use>
				</svg>
			</li>
		</ul>
		<div class="mv-list-progress">
			<span @click="changeOffset('page')">上页</span>
			<input v-model="textOffset" type="text" @keydown.enter="offset = (textOffset-1) * 10">
			<span @click="changeOffset('next')">下页</span>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				type: ['内地','港台','欧美','日本','韩国'],
				typeNew: '内地',
				offset: 0,
				MvList: [],
				textOffset: 1, 
			}
		},
		created() {
			this.getMvList();
		},
		watch: {
			typeNew: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.getMvList();
				}
			},
			
			offset: function(newValue,oldValue) {
				if(newValue != oldValue) {
					this.getMvList();
				}
			},
		},
		methods: {
			// 获取MV排行榜
			getMvList() {
				this.axios({
					url: "https://autumnfish.cn/top/mv",
					params: {
						area: this.typeNew,//内地,港台,欧美,日本,韩国
						offset: this.offset, //数据在第几页
					}
				})
				.then(res => {
					this.MvList = res.data.data;
				})
			},
			
			changeOffset(state) {
				if(state == 'next') {
					this.offset += 30;
				}else if(state == 'page' && this.offset >= 30){
					this.offset -= 30;
				}
				this.textOffset = (this.offset/30)+1;
			},
			
			// 点击MV
			recommended(item) {
				this.getMvURL(item.id)
			},
			
			// 获取MV播放地址
			getMvURL(id) {
				this.axios.get("https://autumnfish.cn/mv/url?id=" + id)
				.then(res => {
					window.open(res.data.data.url);  
				})
				
			},
		},
	}
</script>

<style lang="scss">
	.mv-list {
		overflow: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		
		.mv-list-titel {
			margin-bottom: 1%;
			
			span {
				color: #8d91ab;
			}
			
			span.active {
				color: #f3fcff;
			}
		}
		
		.mv-list-content {
			width: 100%;
			text-align: center;
			
			li {
				display: inline-block;
				position: relative;
				width: 20%;
				margin-bottom: 2%;
				
				img {
					width: 80%;
					border-radius: 10%;
				}
				p {
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					display: inline-block;
					width: 70%;
					margin: 5% 0 4%;
					color: #b5bed6;
					font-size: 1em;
				}
				
				svg {
					display: none;
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					color: #d2d2ff; 
					margin: auto;
					font-size: 3em;
					cursor: pointer;
				}
				
				&:hover svg {
					display: block;
				}
			}
		}
		
		.mv-list-progress {
			text-align: center;
			
			input {
				width: 2em;
				margin: 0 1em;
				padding: .5em;
				border: 0px;
				outline: none;
				border-radius: 10px;
				background-color: rgba(106, 138, 255, 0.4);
			}
		}
	}
</style>
