import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		audioMusicId: '',
		audioMusicUrl: '',
		audioMusicInfo: [],
		audioMusicLyrics: [],
		audioMusiclyricsTime: '',
		musicCollectionArr: [],
		musicSearchSong: '',
		musicSearchSinger: '',
		isSearch: true,
		musicNewCommendedArr: [],
		musicListNumber: '',
		playBool: false,
	},
	mutations: {
		// 修改播放的音乐id
		changeId(state, id) {
			state.audioMusicId = id;
		},
		// 修改播放地址
		changeUrl(state, url) {
			state.audioMusicUrl = url;
		},
		// 修改播放的音乐信息
		changeInfo(state, info) {
			state.audioMusicInfo = info;
		},
		// 修改播放时的秒数
		changeInfolyricsTime(state, lyricsTime) {
			state.audioMusiclyricsTime = lyricsTime;
		},
		// 修改播放的歌词
		changeInfolyrics(state, Lyrics) {
			state.audioMusicLyrics = Lyrics;
		},
		// 播放时,对应到该歌词的颜色
		changeInfolyricsColor(state, LyricsTime) {
			for (let key in state.audioMusicLyrics) {
				state.audioMusicLyrics[key].bool = false;
				if (state.audioMusicLyrics[key].time === LyricsTime) {
					state.audioMusicLyrics[key].bool = true;
				}
			}
		},
		// 修改歌单数据
		changeCollectionArr(state, arr) {
			state.musicCollectionArr = arr;
		},
		// 修改搜索词
		changeSearch(state, value) {
			state.musicSearchSong = value;
		},
		// 修改搜索歌手
		changeSearchSinger(state, value) {
			state.musicSearchSinger = value;
		},
		// 更新新歌推荐内容
		changeCommended(state, arr) {
			state.musicNewCommendedArr = arr;
		},
		
		// 更改播放的歌单列表
		changeMusicList(state,number) {
			state.musicListNumber = number;
		},
		
		// 更改搜索类型
		changeIsSearch(state,bool) {
			state.isSearch = bool;
		},
		
		changePlayBool(state,bool) {
			state.playBool = bool;
		}
	},
	actions: {
		Clean({commit}) {
			commit("changeUrl",'')
			commit("changeId",'');
			commit("changeInfo",[]);
			commit("changeInfolyricsTime", 0);
			commit("changeInfolyrics", []);
		},
	},
	modules: {

	}
})
