<template>
	<div class="playlist-detailed">
		<div class="playlist-detailed-left">
			<!-- 歌单信息 -->
			<div class="playlist-detailed-info">
				<img :src="playLisInfo.coverImgUrl" alt="">
				<div>
					<p :title="playLisInfo.name" style="font-size: 1.8em; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;">{{playLisInfo.name}}</p>
					<p style="white-space: nowrap; overflow: hidden; text-overflow: ellipsis; color: #a7a9b9;">
						<span style="color: #e1e1e1; ">作者：</span> {{nickname}}
					</p>
					<p style="color: #9392a6;">
						<span style="color: #e1e1e1; ">标签：</span><span style="color: #7389ad;" v-for="(v,i) in playLisInfo.tags" :key="i"> {{v}} </span>
					</p>
					<p :title="playLisInfo.description" style="white-space: nowrap; overflow: hidden; text-overflow: ellipsis; color: #9f9fb4;">
						<span style="color: #e1e1e1; ">简介：</span>{{playLisInfo.description}}
					</p>
				</div>
			</div>
			<!-- 歌单推荐 -->
			<div class="playlist-detailed-commended">
				<div v-for="(item,index) in relatedPlayList" :key="index" @click="switchPlayList(item)">
					<img :src="item.coverImgUrl" :alt="item.name">
					<p>{{item.name}}</p>
				</div>
			</div>
		</div>
		<!-- 歌单歌曲 -->
		<div class="playlist-detailed-songs">
			<p style="color: #d4d4d4; margin-bottom: 1%;">歌曲列表</p>
			<ul>
				<li v-for="(item,index) in playListSongs" :key="index">
					<span>{{(index+1)}}</span>
					<!-- <img :src="item.al.picUrl" :alt="item.name"> -->
					<span style="display: inline-block; width: 20%; color: #dfdfdf; font-size: 1.2em; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;">{{item.name}}</span>
					<i v-for="(v,i) in item.ar" :key="i" @click="clickSinger(v)"> {{v.name}} </i>
					<svg style="float: right; font-size: 1.4em; cursor: pointer;" class="icon" aria-hidden="true" @click="play(item)">
						<use xlink:href="#icon-bofang"></use>
					</svg>
				</li>
			</ul>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				playListSongs: [],
				relatedPlayList: [],
				playLisInfoIds: [],
				playLisInfo: [],
				nickname: '',
			}
		},
		props: ['playListId'],
		created() {
			this.get(this.playListId)
		},
		methods: {
			get(id) {
				this.getlist(id);
				this.getRelatedPlayList(id)
			},
			
			// 获取歌单内容
			getlist(id) {
				this.axios({
					url: 'https://autumnfish.cn/playlist/detail?id=' + id,
				}).then(res => {
					this.playLisInfo = res.data.playlist;
					this.nickname = res.data.playlist.creator.nickname
					let ids = [];
					for (let key in res.data.playlist.trackIds) {
						ids.push(res.data.playlist.trackIds[key].id);
					}
					// 获取歌单歌曲
					this.axios({
						url: 'https://autumnfish.cn/song/detail?ids=' + ids
					}).then(res => {
						this.playListSongs = res.data.songs
					})
				})
				
			},
			
			// 相关歌单推荐
			getRelatedPlayList(id) {
				// id是其他歌单的ID
				this.axios({
					url: 'https://autumnfish.cn/related/playlist?id=' + id,
				}).then(res => {
					this.relatedPlayList = res.data.playlists;
				})
			},
			
			// 去播放
			play(value) {
				// console.log(value)
				this.$store.commit('changeId',value.id)
			},
			
			// 点击歌手
			clickSinger(value) {
				// console.log(value)
				this.$store.commit('changeSearch', value.name)
				this.$store.commit('changeIsSearch', false);
				this.$store.commit('changeSearchSinger', value);
				this.$emit('goback')
			},
			
			// 切换歌单
			switchPlayList(value) {
				this.get(value.id)
			}
		},
	}
</script>

<style lang="scss">
	.playlist-detailed {
		height: 100%;
		padding: 1% 4%;
		// display: flex;

		.playlist-detailed-left,
		.playlist-detailed-songs {
			display: inline-block;
			width: 50%;
			height: 100%;
			float: left;
		}

		.playlist-detailed-left .playlist-detailed-info {
			width: 100%;
			height: 50%;

			img {
				// display: block;
				// width: 30%;
				height: 50%;
				margin-right: 5%;
				vertical-align: top;
				border-radius: 20px;
			}

			div {
				width: 80%;
				height: 40%;
				line-height: 2em;
				padding-top: 4%;
				box-sizing: border-box;
			}
		}
		
		.playlist-detailed-left .playlist-detailed-commended {
			overflow: hidden;
			overflow-y: scroll;
			width: 100%;
			height: 40%;
			
			div {
				display: inline-block;
				width: 30%;
				margin-bottom: 5%;
				text-align: center;
				
				img {
					width: 70%;
					border-radius: 20px;
				}
				
				p {
					padding: 5%;
					color: #acacc3;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
		}
		
		.playlist-detailed-songs ul {
			overflow: hidden;
			overflow-y: scroll;
			height: 94%;
		}
		
		.playlist-detailed-songs ul li {
			padding: 1.5% 2% 1.5% .5%;
			color: #9e9e9e;
			
			img {
				display: inline-block;
				width: 5%;
				margin-right: 2%;
				border-radius: 50%;
				vertical-align: middle;
			}
			
			span {
				margin-right: 4%;
			}
		}

		.playlist-detailed-songs ul li:nth-child(odd) {
			background-color: rgba(255, 255, 255, .04);
		}

		.playlist-detailed-songs ul li:hover {
			background-color: rgba(255, 255, 255, .2);
		}
	}
</style>
